import AniButton from '../../components/button/AniButton';
import { LinkItem } from '../../components/item/LinkItem';
import { useFireUser } from '../../firebase/useFireUser';

export const MemoMain = (props) => {
  const { user } = useFireUser();

  //새 메모 추가
  function onNewMemoBtn() {
    // memoDataLoad();
    props.onNewMemoBtn();
    return false;
  }

  const homemng =
  user && user.uid === 'I3cTgdCxwjhBBno4mtZYvnODPJu1' ? (
    <LinkItem to="/homemng"> 홈페이지 관리</LinkItem>
  ) : (
    ''
  );

  return (
    <>
      <h3>{props.title}</h3>
      사이드 메뉴에서
      <AniButton
        disableElevation
        fullWidth
        // size="large"
        type="button"
        variant="outlined"
        color="success"
        onClick={onNewMemoBtn}
      >
        새글
      </AniButton>
      버튼을 사용해 메모를 작성합니다.
      {homemng}
    </>
  );
};

export default MemoMain